import React, { Component } from "react"
import SEO from "../components/seo"
import styled from "@emotion/styled"
import Img from 'gatsby-image'
import { graphql } from "gatsby"
import lbg from "../images/loadingbadge.png"

export const MainImage = styled(Img)`
  max-height: 50vh;
  margin: 0 auto;
  @media only screen and (min-width: 1400px) {
    width: 1280px;
  }
`

export const query = graphql`
  query {
    mainBg: file(relativePath: { eq: "mainbg.png" }) {
      childImageSharp {
        fluid(maxWidth: 800) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`

const L2DCanvas = styled.canvas`
  width: 40vh;
  height: 65vh;
  min-width: 302px;
`

const MainPage = styled.div`
  padding-top: 15px;
`

const WelcomeTitle = styled.h1`
  font-size: x-large;
`

const LoadingWrapper = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: -1;
  width: 20%;
  height: 21%;
`

const LoadingIcon = styled.div`
  background-image: url(${lbg});
  background-position: top 5% center;
  background-repeat: no-repeat;
  background-size: contain;
`

const MainContent= styled.div`
  position: relative;
`


class IndexPage extends Component {
  componentDidMount () {
      const script = document.createElement("script");
      script.src = "lib/live2d/l2d.min.js";
      script.id = "l2dScript"
      document.body.appendChild(script);
  }

  componentWillUnmount() {
    const script = document.getElementById("l2dScript");
    script.remove();
  }

  render() {
    return (
      <>
        <MainPage>
          <SEO title="Home" />
          <WelcomeTitle>Welcome</WelcomeTitle>
          <MainContent id={'content'} className="main-content"  >
            <div id={'l2d_canvas'} className="sub-content">
              <button hidden id={'btnChange'} className="active"></button>
              <L2DCanvas id={'glcanvas'} className="glcanvas">
              </L2DCanvas>
            </div>
            <LoadingWrapper>
              <LoadingIcon>
                  <svg viewBox="0 0 100 106">
                    <polyline className="line-cornered stroke-animation " points="89,28 50,5 11,27 11,72 50,95 89,73 89,28 50,5" strokeWidth="10"
                              fill="none"></polyline>
                    <polyline className="line-cornered stroke-animation " points="11,72 50,95 89,73 89,28 50,5 11,27 11,72 50,95" strokeWidth="10"
                              fill="none"></polyline>
                  </svg>
              </LoadingIcon>
            </LoadingWrapper>
          </MainContent>
          <p>I'm the mascot of this site, Hourai Etealune</p>
        </MainPage>
      </>
    )
  }
}

export default IndexPage
